import Component from "../classes/Component";
import { $dom } from "../helpers/dom";
import { $events } from "../helpers/events";
import variables from "../variables";

const { getAll, hasClass, addClass, removeClass, each, attr } = $dom;

const headerLinkActiveClassName = 'header__nav-link--active'

const { header } = variables.dom;

const linkDomElements = getAll('.header__nav-link');
const sectionDomElements = getAll('.js-scroll-anchor');

let lastId = null;

export function smartScroll(
  rootSelector = '#js-header'
) {

  const scrollHandler = () => {

    let scrollDistance = window.scrollY;

    let currentItem = sectionDomElements.filter(section => section.offsetTop - header.clientHeight < scrollDistance)

    currentItem = currentItem[currentItem.length - 1];

    let currentId = attr(currentItem, 'id');

    if (lastId !== currentId) {
      lastId = currentId;

      each(linkDomElements, element => {
        if (hasClass(element, headerLinkActiveClassName)) {
          removeClass(element, headerLinkActiveClassName);
        }
      })

      let filteredLinks = linkDomElements.filter(element => attr(element, 'href') === `#${currentId}`);

      addClass(filteredLinks, headerLinkActiveClassName);
    }
  }

  return new Component({
    name: 'smartScroll',
    requiredSelector: rootSelector,

    onInit() {
      $events.add('scroll', window, scrollHandler)
    },
    onDestroy() {
      $events.remove('scroll', window, scrollHandler)
    },
  });
}



