import Component from "../classes/Component";
import { $dom } from "../helpers/dom";
import { $events } from '../helpers/events';
import { isElement } from "../helpers/_utilities";

const { get, addClass, removeClass, hasClass, attr } = $dom;

export function siteSearch(
  rootSelector = '.js-search'
) {

  const
    searchElement = get(rootSelector),
    activeClass = 'is-active',

    buttonSubmitSelector = '.js-search-submit',
    buttonCloseSelector = '.js-search-close'
    ;

    let buttonType = null;

  const clickHandler = (event) => {
    
    const { target } = event;
    const submitButton = target.closest(buttonSubmitSelector);
    const closeButton = target.closest(buttonCloseSelector);

    if (isElement(submitButton)) {
      
      addClass(searchElement, activeClass);

      buttonType = 'submit';
    }

    if (isElement(closeButton)) {
      
      removeClass(searchElement, activeClass);

      buttonType = 'button';
    }
  }

  const buttonTypeSwapper = event => {
    attr(get(buttonSubmitSelector, event.target.closest(rootSelector)), 'type', buttonType);
    
  }

  return new Component({
    name: 'siteSearch',
    requiredSelector: rootSelector,

    onInit() {
      $events.delegate
        .on('click tap', rootSelector, clickHandler)
        .on('transitionend', '.header__search-wrapper', buttonTypeSwapper)
    },
    onDestroy() {
      $events.delegate
        .off('click tap', rootSelector, clickHandler)
        .off('transitionend', '.header__search-wrapper', buttonTypeSwapper)
    },
  })
}