import { BootstrapOffcanvas } from "./bootstrap";
import Component from "../classes/Component";
import { $dom } from "../helpers/dom";
import { $events } from '../helpers/events';
import variables from "../variables";
import { isElement } from "../helpers/_utilities";

const { menu } = variables.dom;
const { attr, get, addClass, removeClass, hasClass } = $dom;

export function siteMenu(
  rootSelector = '#siteMenu'
) {

  const offcanvasMenu = BootstrapOffcanvas.getOrCreateInstance(menu);
  const burgerButtonElement = get('.header__menu-burger');
  const burgerButtonClass = 'header__menu-burger--active';
  let lastAnchor = null;
  let hideMenuTimeout = null;

  function hideMenuHandler() {

    if(hasClass(burgerButtonElement, burgerButtonClass)) {
      removeClass(burgerButtonElement, burgerButtonClass);
    }

    if (isElement(lastAnchor)) {
      const href = attr(lastAnchor, 'href')
      const section = get(href)
      const yScrollDistance = window.scrollY + section.getBoundingClientRect().top;
    
      hideMenuTimeout = setTimeout(() => {
        window.scrollTo({
          top: yScrollDistance,
          behavior: "smooth",
        })
        clearTimeout(hideMenuTimeout);
      }, 0)  
    }
  }

  const clickHandler = (event) => {
    
    const menuLink = event.target.closest('.menu-link');

    if (Boolean(menuLink)) {
      event.preventDefault();
      offcanvasMenu.hide();
      lastAnchor = menuLink;
    }
  }

  const openMenuHundler = () => {
    if(!hasClass(burgerButtonElement, burgerButtonClass)) {
      addClass(burgerButtonElement, burgerButtonClass);
    }
  }

  return new Component({
    name: 'siteMenu',
    requiredSelector: rootSelector,

    onInit() {
      $events.delegate
        .on('click tap', rootSelector, clickHandler)
        .on('hidden.bs.offcanvas', rootSelector, hideMenuHandler)
        .on('show.bs.offcanvas', rootSelector, openMenuHundler)
    },
    onDestroy() {
      $events.delegate
        .off('click tap', rootSelector, clickHandler)
        .off('hidden.bs.offcanvas', rootSelector, hideMenuHandler)
        .off('show.bs.offcanvas', rootSelector, openMenuHundler)
    },
  });
}