import Component from "../classes/Component";
import { $dom } from "../helpers/dom";
import { $style } from "../helpers/style";
import { $events } from "../helpers/events";

const { get } = $dom
const { set } = $style

export function siteScrollProgressBar(
  rootSelector = '.js-progressbar'
) {

  const htmlElement = $dom.get('html');
  const progressbar = get('.header__progressbar');

  const siteScrollHandler = () => {
    let documentScroll = htmlElement.scrollTop;
    let documentHeight = htmlElement.scrollHeight - htmlElement.clientHeight;
    let currentScroll = (documentScroll / documentHeight) * 100;

    set(progressbar, 'width', currentScroll + "%");
  }

  return new Component({
    name: 'siteScrollProgressBar',
    requiredSelector: rootSelector,

    onInit() {
      $events.add('scroll', window, siteScrollHandler);
    },
    onDestroy() {
      $events.remove('scroll', window, siteScrollHandler);
    },
  })
}